import React,{useState} from 'react'
import axios from 'axios'
import dateFormat from 'dateformat'

// importing images
import trash from "../images/trash.png"
import recycle from "../images/recycle.png"
import approve from "../images/approve.png"

function Post({message,id,setIsLoading,shared,targetName,isAdmin,onlySharedPosts,setOnlySharedPosts,posts,notSharedPosts,setNotSharedPosts,requestToShareAt,sharedAt}) {

    const [isShared,setIsShared] = useState(shared)
    const baseUrlOfApi = 'https://sspotted.herokuapp.com'
    //http://localhost:5000

    // DELETE HANDLER
    const deleteHandler = async(e) => {
        setIsLoading(true)
        const requestOptions = { method:'DELETE' }
        const res = await fetch(`${baseUrlOfApi}/posts/${id}`,requestOptions)
        const data = await res.json();
        if(data.ok === 1){
            const post = e.target.offsetParent.offsetParent;
            post.style.display ="none"
            if(shared){
                const newSharedPosts = onlySharedPosts.filter((post)=>post._id !== id)
                setOnlySharedPosts([...newSharedPosts])
            }else{
                const newNotSharedPosts = notSharedPosts.filter((post)=>post._id !== id)
                setNotSharedPosts([...newNotSharedPosts])
            }
        }else{
            console.log("oh ops");
        }
        setIsLoading(false)
        
    }

    // SHARE HANDLER "PATCH"
    const shareHandler = async (e) => {
        setIsLoading(true)
        await axios.patch(`${baseUrlOfApi}/posts/${id}`,{shared:!isShared})
        setIsShared(!isShared)
        const el = e.target
        const thisPost = posts.filter((post) => post._id === id)
        if(el.innerText === "Share") {
            el.innerText = "Shared"
            const post = e.target.offsetParent.offsetParent;
            post.style.display ="none"
            // setOnlySharedPosts([...onlySharedPosts,...thisPost])
            
            
        }else{
            el.innerText = "Share"
            // setOnlySharedPosts( posts.filter((post) => post._id !== id && post.shared === true))
            
        }
        if(shared){
            thisPost[0].shared = false
            const newSharedPosts = onlySharedPosts.filter((post)=> post._id !== id )
            setOnlySharedPosts([...newSharedPosts])
            setNotSharedPosts([...notSharedPosts,...thisPost])
        }else {
            // make a request to backend and change the shared_at value
            const date = new Date();
            const dateformatted = dateFormat(date, "ddd, mmm dS, yyyy, h:MM TT")
            await axios.patch(`${baseUrlOfApi}/posts/update-shared-at/${id}`,{shared_at:dateformatted})
            thisPost[0].shared = true
            thisPost[0].shared_at = dateformatted
            const newNotSharedPosts = notSharedPosts.filter((post)=>post._id !== id)
            setNotSharedPosts([...newNotSharedPosts])
            setOnlySharedPosts([...onlySharedPosts,...thisPost])
        }
        setIsLoading(false)
        
    }
    return (
        <div className="post" id={"a"+id}>

            {isAdmin && <h2 className="requestDate">{requestToShareAt}</h2>}
            {shared && <h2 className="requestDate">{sharedAt}</h2>}
            <div className="postContent">

                <div className="targetNameHolder">
                    <h1>for : {targetName}</h1>
                </div>

                <div className="messageHolder">
                    <p className="postMessage" id="postMessage">
                        {message}
                    </p>
                </div>

                {isAdmin?
                    <div className="postOptions">
                        <button onClick={deleteHandler}><img src={trash} alt="delete" id="delete"/></button>
                        <button onClick={shareHandler}>{shared?<img src={recycle} alt="shared" id="shared"/>:<img src={approve} alt="share" id="share"/>}</button>
                    </div>
                :"" }
            </div>
        </div>

    )
}

export default Post
